.person_container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    padding-top: 10px;
    padding-bottom: 20px;
}

.person_card {
    position: relative;
    padding: 10px;
    margin: 3px;
    width: 235px;
    height: 290px;
    cursor: pointer;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
}

.person_card img {
    display: block;
    width: 100%;
    height: auto;
}

.name {
    position: absolute;
    padding: 10px;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 65px;
    background-color: #fff;
}